/**
 * @fileoverview This file contains the database connection configuration and utilities.
 * It uses the 'pg' library to connect to a PostgreSQL database.
 * The connection details are sourced from environment variables for security.
 */

import { Pool } from 'pg';

// Create a new connection pool.
// A pool is better than a single client for web applications
// because it can manage multiple connections at once.
const pool = new Pool({
  user: process.env.DB_USER || 'postgres',
  host: process.env.DB_HOST || 'localhost',
  database: process.env.DB_NAME || 'employeedb',
  password: process.env.DB_PASSWORD || 'password',
  port: parseInt(process.env.DB_PORT || '5432', 10),
});

/**
 * Executes a query against the database.
 * @param {string} text - The SQL query string.
 * @param {any[]} [params] - Optional parameters for the SQL query.
 * @returns {Promise<any>} A promise that resolves with the query result.
 */
export const query = (text, params) => {
    return pool.query(text, params);
};


export async function initializeDb() {
    let client;
    try {
        client = await pool.connect();
        await client.query('BEGIN');

        // Check if employees table exists
        const employeesTableExists = await client.query(`
            SELECT EXISTS (
                SELECT FROM information_schema.tables 
                WHERE  table_name   = 'employees'
            );
        `);

        if (!employeesTableExists.rows[0].exists) {
            console.log("Creating database schema...");
            await client.query(`
                -- Table for storing basic employee data
                CREATE TABLE employees (
                    id VARCHAR(255) PRIMARY KEY,
                    first_name VARCHAR(255) NOT NULL,
                    last_name VARCHAR(255) NOT NULL,
                    middle_name VARCHAR(255),
                    position VARCHAR(255),
                    department VARCHAR(255),
                    branch VARCHAR(255),
                    gender VARCHAR(50),
                    date_of_birth DATE,
                    date_hired DATE,
                    status VARCHAR(50) DEFAULT 'Active', -- e.g., Active, On Leave, Resigned
                    photo_url TEXT,
                    password TEXT
                );

                -- Table for HR personnel accounts
                CREATE TABLE hr_personnel (
                    id SERIAL PRIMARY KEY,
                    first_name VARCHAR(255) NOT NULL,
                    last_name VARCHAR(255) NOT NULL,
                    email VARCHAR(255) UNIQUE NOT NULL,
                    password TEXT NOT NULL
                );
                
                -- Table for Administrator accounts
                CREATE TABLE admins (
                    id SERIAL PRIMARY KEY,
                    first_name VARCHAR(255) NOT NULL,
                    last_name VARCHAR(255) NOT NULL,
                    email VARCHAR(255) UNIQUE NOT NULL,
                    password TEXT NOT NULL
                );

                -- Table for attendance records
                CREATE TABLE attendance (
                    id SERIAL PRIMARY KEY,
                    employee_id VARCHAR(255) REFERENCES employees(id),
                    attendance_date DATE NOT NULL,
                    time_in TIMESTAMP,
                    time_out TIMESTAMP,
                    status VARCHAR(50), -- e.g., Present, Late, Absent
                    notes TEXT,
                    UNIQUE(employee_id, attendance_date)
                );
                
                -- Table for schedules
                CREATE TABLE schedules (
                    id SERIAL PRIMARY KEY,
                    employee_id VARCHAR(255) REFERENCES employees(id),
                    week_start_date DATE NOT NULL,
                    schedule_data JSONB -- Stores the entire week's schedule as a JSON object
                );

                -- Table for payslips
                CREATE TABLE payslips (
                    id SERIAL PRIMARY KEY,
                    employee_id VARCHAR(255) REFERENCES employees(id),
                    period_start DATE NOT NULL,
                    period_end DATE NOT NULL,
                    pay_date DATE,
                    earnings JSONB,
                    deductions JSONB,
                    net_pay NUMERIC(10, 2)
                );
                
                -- Table for leave requests
                CREATE TABLE leave_requests (
                    id SERIAL PRIMARY KEY,
                    employee_id VARCHAR(255) REFERENCES employees(id),
                    leave_type VARCHAR(100),
                    start_date DATE,
                    end_date DATE,
                    reason TEXT,
                    status VARCHAR(50) DEFAULT 'Pending' -- e.g., Pending, Approved, Rejected
                );

                -- Table for company-wide announcements
                CREATE TABLE announcements (
                    id SERIAL PRIMARY KEY,
                    title VARCHAR(255) NOT NULL,
                    content TEXT,
                    posted_by INTEGER REFERENCES hr_personnel(id),
                    posted_at TIMESTAMP DEFAULT CURRENT_TIMESTAMP,
                    status VARCHAR(50) DEFAULT 'Draft' -- e.g., Draft, Published
                );
            `);

            console.log("Inserting sample data...");
            /**
             * Default User Accounts for Testing:
             * 
             * 1. Employee Account
             *    - Identifier: EMP-00123
             *    - Password:   password
             * 
             * 2. HR Account
             *    - Identifier: olivia.chen.hr@example.com
             *    - Password:   password
             * 
             * 3. Admin Account
             *    - Identifier: super@example.com
             *    - Password:   password
             */
            await client.query(`
                INSERT INTO employees (id, first_name, last_name, position, department, branch, status, password, date_hired) VALUES 
                ('EMP-00123', 'Maria', 'Rodriguez', 'Senior Software Engineer', 'Technology', 'Headquarters', 'Active', 'password', '2022-01-15'),
                ('EMP-00124', 'David', 'Lee', 'Project Manager', 'Management', 'Headquarters', 'Active', 'password', '2021-03-20'),
                ('EMP-00125', 'Sarah', 'Chen', 'UX Designer', 'Design', 'Downtown Office', 'Active', 'password', '2023-05-10'),
                ('EMP-00126', 'John', 'Doe', 'QA Tester', 'Technology', 'Remote', 'On Leave', 'password', '2022-08-01');

                INSERT INTO hr_personnel (first_name, last_name, email, password) VALUES 
                ('Olivia', 'Chen', 'olivia.chen.hr@example.com', 'password');

                INSERT INTO admins (first_name, last_name, email, password) VALUES 
                ('Super', 'Admin', 'super@example.com', 'password');

                INSERT INTO announcements (title, content, posted_by, status) VALUES
                ('Annual Company Retreat 2024', 'We are excited to announce that this years company retreat will be held at Lakeview Resort from October 10th to 12th. More details to follow!', 1, 'Published'),
                ('New Health Insurance Policy', 'Please review the updated health insurance policy documents available on the employee portal. The new policy will be effective starting next month.', 1, 'Published'),
                ('Q3 Performance Reviews', 'The Q3 performance review cycle will begin next week. Please schedule a meeting with your manager to discuss your progress.', 1, 'Draft');

                INSERT INTO attendance (employee_id, attendance_date, time_in, time_out, status) VALUES
                ('EMP-00123', CURRENT_DATE - 3, (CURRENT_DATE - 3) + interval '9 hour', (CURRENT_DATE - 3) + interval '17 hour', 'Present'),
                ('EMP-00123', CURRENT_DATE - 2, (CURRENT_DATE - 2) + interval '9 hour 15 minute', (CURRENT_DATE - 2) + interval '17 hour 5 minute', 'Late'),
                ('EMP-00123', CURRENT_DATE - 1, (CURRENT_DATE - 1) + interval '8 hour 58 minute', (CURRENT_DATE - 1) + interval '17 hour 2 minute', 'Present');

                INSERT INTO leave_requests (employee_id, leave_type, start_date, end_date, reason, status) VALUES
                ('EMP-00123', 'Vacation', '2024-09-01', '2024-09-05', 'Family trip to Japan.', 'Approved'),
                ('EMP-00124', 'Sick Leave', CURRENT_DATE - 1, CURRENT_DATE -1, 'Feeling unwell, have a fever.', 'Approved'),
                ('EMP-00125', 'Personal Day', CURRENT_DATE + 5, CURRENT_DATE + 5, 'Important personal appointment.', 'Pending'),
                ('EMP-00123', 'Bereavement', '2024-07-20', '2024-07-22', 'Family emergency.', 'Rejected');
                
                INSERT INTO schedules (employee_id, week_start_date, schedule_data) VALUES
                ('EMP-00123', date_trunc('week', current_date), 
                    '[
                        {"day": "Monday", "date": "Aug 19", "timeIn": "09:00", "timeOut": "17:00", "break": "1 hour", "hours": 7},
                        {"day": "Tuesday", "date": "Aug 20", "timeIn": "09:00", "timeOut": "17:00", "break": "1 hour", "hours": 7},
                        {"day": "Wednesday", "date": "Aug 21", "timeIn": "09:00", "timeOut": "17:00", "break": "1 hour", "hours": 7},
                        {"day": "Thursday", "date": "Aug 22", "timeIn": "09:00", "timeOut": "17:00", "break": "1 hour", "hours": 7},
                        {"day": "Friday", "date": "Aug 23", "timeIn": "09:00", "timeOut": "17:00", "break": "1 hour", "hours": 7},
                        {"day": "Saturday", "date": "Aug 24", "timeIn": "OFF", "timeOut": "OFF", "break": "-", "hours": 0},
                        {"day": "Sunday", "date": "Aug 25", "timeIn": "OFF", "timeOut": "OFF", "break": "-", "hours": 0}
                    ]'::jsonb
                );

                INSERT INTO payslips (employee_id, period_start, period_end, pay_date, earnings, deductions, net_pay) VALUES
                ('EMP-00123', (date_trunc('month', current_date) - interval '1 month'), (date_trunc('month', current_date) - interval '1 day'), current_date,
                    ' [
                        {"name": "Basic Salary", "amount": 75000.00},
                        {"name": "Overtime Pay", "amount": 5000.00},
                        {"name": "Transportation Allowance", "amount": 2000.00}
                    ]'::jsonb,
                    ' [
                        {"name": "Tax", "amount": 8500.00},
                        {"name": "Social Security", "amount": 1500.00},
                        {"name": "Health Insurance", "amount": 1000.00}
                    ]'::jsonb,
                    61000.00
                );
            `);
            console.log("Database initialized successfully.");
        } else {
            console.log("Database schema already exists.");
        }

        await client.query('COMMIT');
    } catch (e) {
        console.error("Database initialization failed:", e.message);
        if (client) {
            await client.query('ROLLBACK');
        }
    } finally {
        if (client) {
            client.release();
        }
    }
}
